import React, { useState } from "react";
import styled, { ThemeProvider } from "styled-components";
//import theme from "../../../theme";
import { useNavigate } from "react-router-dom";
import { Container, CssBaseline } from '@mui/material';
import { Box } from '@mui/material';
import { Grid } from '@mui/material';
import { Typography } from '@mui/material';
import { TextField } from '@mui/material';
import { Button } from '@mui/material';
import { Link } from '@mui/material';

//import { CssBaseline } from "@mui/material-ui/core";


function LoginPage(props) {
    const navigate = useNavigate();

    return (
        
        /*
            Container   layout 
            , contents     .
            fluid -> maxWidth=" " 
            fixed -> fixed  . 
             Container     
        */
        <Container
            maxWidth='md'
        >
            {/* 
                Box <Box></Box>   tag  css  
                css   sx={{}} 
                 Box       
            */}
            <Box
                sx={{ 
                    height: '100vh',
                    display: 'flex',
                    alignItems: 'center', //  
                }}
            >
                {/* 
                    Grid Component    . 
                      Grid  Grid . ->    
                */}
                {/*  Grid -> <Grid container> 
                     spacing item   -> 0 10     
                    spacing={0}      */}
                <Grid container spacing={0}> 
                    {/*  Grid -> <Grid item>
                        xs breakpoint     
                        xs={12} sm={6}  12   6   */}
                    <Grid item xs={12} sm={6}>
                        <Box 
                            sx={{
                                height: '60vh',
                                backgroundColor: '#fafafa',
                                display: 'flex',
                                flexDirection: 'column', // direction column  
                                alignItems: 'center', //    .
                                borderRadius: '10px 0px 0px 10px',
                                boxShadow: 'rgba(0, 0, 0, 0.9) 23px 10px 15px -5px'
                            }}
                        >
                            {/* 
                              
                            <Typography component="h1" variant="h5"> :  h4 h1   
                            */}
                            {/* 
                                Typography text    
                                h1, p tag...    -> variant  .
                                 variant="h3" -> <h3></h3> 
                             */}
                            <Typography  
                                variant="h3"
                                style={{
                                    marginTop: '16%',
                                    marginBottom: 10,
                                    color: '#424242'
                                }}    
                            >
                                Login
                            </Typography>
                            {/*
                                TextField input component.
                                type  input     
                            */}
                            <TextField 
                                label="ID" //labeling 
                                type="text" // text
                                name="type" 
                                required //  
                                autoFocus //   
                                margin="normal"
                                style={{
                                    backgroundColor: 'white'
                                }}
                            />
                            <TextField 
                                label="PW" 
                                type = "password" // 
                                name="password" 
                                required 
                                margin="normal"
                                style={{
                                    marginTop: 5,
                                    backgroundColor: 'white'
                                }}
                            />
                            <Button 
                                type="submit" 
                                variant="outlined" 
                                sx={{ //css 
                                    mt: 3,
                                    pr: 11,
                                    pl: 11,
                                    color: 'white',
                                    border: '1px solid skyblue',
                                    borderRadius: '10px',
                                    backgroundColor: 'skyblue',
                                    // "&.Mui[mui]-root:[event ]" : {}   css   .
                                    // "&.MuiButton-root:hover" : {}   css 
                                    "&.MuiButton-root:hover":{
                                        color: 'skyblue',
                                        borderColor: 'skyblue'
                                    }
                                }}
                                onClick={() => {
                                    navigate("/main");
                                  }}
                            >
                                
                            </Button>
                            <a
                                type="button"
                                variant="outlined"
                                sx={{ //css 
                                    mt: 3,
                                    pr: 11,
                                    pl: 11,
                                    color: 'white',
                                    border: '1px solid skyblue',
                                    borderRadius: '10px',
                                    backgroundColor: 'skyblue',
                                    // "&.Mui[mui]-root:[event ]" : {}   css   .
                                    // "&.MuiButton-root:hover" : {}   css 
                                    "&.MuiButton-root:hover":{
                                        color: 'skyblue',
                                        borderColor: 'skyblue'
                                    }
                                }}
                                href="/oauth2/authorization/google"
                            >
                                 
                            </a>
                            <a
                                type="button"
                                variant="outlined"
                                sx={{ //css 
                                    mt: 3,
                                    pr: 11,
                                    pl: 11,
                                    color: 'white',
                                    border: '1px solid skyblue',
                                    borderRadius: '10px',
                                    backgroundColor: 'skyblue',
                                    // "&.Mui[mui]-root:[event ]" : {}   css   .
                                    // "&.MuiButton-root:hover" : {}   css 
                                    "&.MuiButton-root:hover":{
                                        color: 'skyblue',
                                        borderColor: 'skyblue'
                                    }
                                }}
                                href="/oauth2/authorization/naver"
                            >
                                 
                            </a>
                            {/* Link a tag   */}
                            <Link
                                style={{
                                    marginTop: 15,
                                    color: 'skyblue'
                                }}
                                href="./MainPage"
                                underline="hover"
                            >
                                ID / PW 
                            </Link>
                        </Box>
                    </Grid>
                    <Grid item xs={12} sm={6}> {/*12   6  */}
                        <Box 
                            sx={{
                                height: '60vh',
                                background: 'linear-gradient(to right, #C3E7FA, #91D8FA)',
                                alignItems: 'center',
                                display: 'flex',
                                flexDirection: 'column',
                                borderRadius: '0px 10px 10px 0px',
                                boxShadow: 'rgba(0, 0, 0, 0.9) 10px 10px 15px -5px'
                            }}
                        >
                            <Typography 
                                variant="h4"
                                color="white"
                                style={{
                                    marginTop: '38%'
                                }}  
                            >
                                 !
                            </Typography>
                            <Typography 
                                // variant    
                                color="white"
                                style={{
                                    fontSize: '0.8rem',
                                    marginTop: 20,
                                    marginBottom: 10
                                }}  
                            >
                                  ?
                            </Typography>

                            <Button 
                                type="submit" 
                                variant="outlined" 
                                sx={{
                                    mt: 4,
                                    pr: 6,
                                    pl: 6,
                                    color: 'white',
                                    border: '1px solid white',
                                    borderRadius: '10px',
                                    "&.MuiButton-root:hover":{
                                        backgroundColor: 'white',
                                        color: 'skyblue',
                                        borderColor: 'white'
                                    }
                                }}
                                onClick={() => {navigate("/SignUpPage")}}
                            >
                                
                            </Button>
                        </Box>
                    </Grid>
                </Grid>
            </Box>
        </Container>
        
    );
}

export default LoginPage;